import Mo from '../../../serializer';
import { subject, place, comment, time, topReceivers } from './constants';

const asl = [
  'root',
  [
    'card',
    {
      cardType: 'calendar',
      height: 400,
      spacing: {
        before: 10,
        after: 10,
      },
      metadata: {
        time,
        place,
        comment,
        topReceivers,
        subject,
      },
    },
  ],
];

export default Mo.jsonMLToValue(asl, { normalize: false });
